@import url("https://fonts.googleapis.com/css2?family=Lato&family=Montserrat:wght@700&display=swap");
.App {
  background: rgb(209, 191, 47);
  background: radial-gradient(circle, rgba(209, 191, 47, 0.7987570028) 0%, rgba(70, 211, 252, 0.4598214286) 100%);
  overflow: hidden;
}
#cloud {
  position: absolute;
  width: 100%;
  bottom: -7px;
  color: #CBF3F0;
  z-index: 2;
}

#cloud2 {
  position: absolute;
  width: 102%;
  bottom: 2px;
  left: -2px;
  color: #CBF3F0;
  z-index: 1;
}

h2 {
  font-family: "Montserrat", sans-serif;
}

#opacityAbout {
  opacity: 0;
  transform: translateX(100px);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

#opacityProject {
  opacity: 0;
  transform: translateX(-100px);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

#opacityAbout.appear, #opacityProject.appear {
  opacity: 1;
  transform: translateX(0);
}/*# sourceMappingURL=App.css.map */