@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,300,700)
@import url('https://cdn-uicons.flaticon.com/2.0.0/uicons-brands/css/uicons-brands.css')
* 
  font-family: Open Sans


section 
  width: 100%
  display: inline-block
  background: #333
  height: 60px
  text-align: center
  font-size: 22px
  font-weight: 700
  text-decoration: underline


.footer-distributed
  position: relative
  margin-bottom: 0px
  background: #faf8f8
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12)
  box-sizing: border-box
  width: 100%
  text-align: left
  font: bold 16px sans-serif
  padding: 2rem


.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right
  display: inline-block
  vertical-align: top


/* Footer left */

.footer-distributed .footer-left
  width: 40%


/* The company logo */

.footer-distributed h3
  color:  #5e5555
  font: normal 36px 'Open Sans', cursive
  margin: 0


.footer-distributed h3 span
  color:  lightseagreen


/* Footer links */

.footer-distributed .footer-links
  color:  #929090
  margin: 20px 0 12px
  padding: 0


.footer-distributed .footer-links a
  display:inline-block
  line-height: 1.8
  font-weight:400
  text-decoration: none
  color:  inherit


.footer-distributed .footer-company-name
  color:  #222
  font-size: 14px
  font-weight: normal
  margin: 0


/* Footer Center */

.footer-distributed .footer-center
  width: 35%


.footer-distributed .footer-center i
  
  color: #7f7878
  font-size: 25px
  width: 38px
  height: 38px
  
  text-align: center
  line-height: 42px
  margin: 10px 15px
  vertical-align: middle


.footer-distributed .footer-center i.fa-envelope
  font-size: 17px
  line-height: 38px


.footer-distributed .footer-center p
  display: inline-block
  color: #7c7676
  font-weight:400
  vertical-align: middle
  margin:0


.footer-distributed .footer-center p span
  display:block
  font-weight: normal
  font-size:14px
  line-height:2


.footer-distributed .footer-center p a
  color:  lightseagreen
  text-decoration: none


.footer-distributed .footer-links a:before 
  content: "|"
  font-weight:300
  font-size: 20px
  left: 0
  color: #887d7d
  display: inline-block
  padding-right: 5px


.footer-distributed .footer-links .link-1:before 
  content: none


/* Footer Right */

.footer-distributed .footer-right
  width: 180px
  color:  #92999f


.footer-distributed .footer-company-about
  line-height: 20px
  color:  #92999f
  font-size: 13px
  font-weight: normal
  margin: 0


.footer-distributed .footer-company-about span
  display: block
  color:  #ffffff
  font-size: 14px
  font-weight: bold
  margin-bottom: 20px


.footer-distributed .footer-icons
  margin-top: 25px


.footer-distributed .footer-icons a
  display: inline-block
  width: 65px
  height: 65px
  cursor: pointer
  text-decoration: none
  font-size: 20px
  color: #33383b
  text-align: center
  line-height: 35px

  margin-right: 3px
  margin-bottom: 5px


/* If you don't want the footer to be responsive, remove these media queries */

@media (max-width: 880px) 

  .footer-distributed
    font: bold 14px sans-serif
  

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right
    display: block
    width: 100%
    margin-bottom: 40px
    text-align: center
  

  .footer-distributed .footer-center i
    margin-left: 0
  

